/**editer les informations d'une machine */
import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import MapAdd from '../Maps/MapAdd';
import {
    Card,
    CardBody,
    CardHeader,
    Row,
    Progress,
    Col,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    Form,
    NavItem,
    NavLink, } 
from 'reactstrap';
import classnames from 'classnames';

const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};

/*color for next and previous button */
const previousColor = "inverse"
const nextColor = "success"

const containerStyle = {
    width: '100%',
    height: '400px'
};


class Machine extends Component {
    constructor(props) { 
        super(props);        
    }
    
    toggleStep = this.props.toggleStep
    validateOnChange = this.props.validateOnChange
    hasError = this.props.hasError
    handleSubmit = this.props.handleSubmit
    formRef = this.props.formRef
    swalCallback = this.props.swalCallback

    render() {     
        const nom = this.props.machine.formMachine.nom
        const zone = this.props.machine.formMachine.zone
        const pays = this.props.machine.formMachine.pays
        const ville = this.props.machine.formMachine.ville
        const quartier = this.props.machine.formMachine.quartier
        const rue = this.props.machine.formMachine.rue
        let latitude = this.props.machine.formMachine.latitude
        let longitude = this.props.machine.formMachine.longitude
        const libelle = this.props.machine.selectedModele.libelle
        const description = this.props.machine.selectedModele.description
        const capacite = this.props.machine.selectedModele.capacite
        const dimension = this.props.machine.selectedModele.dimension
        const {longueur, largeur, hauteur} = new Object(dimension)
        console.log(dimension)
        const lat = parseFloat(latitude);
        const lng = parseFloat(longitude);
        const coordonnees = {lat, lng}
        
        // tab item active
        const tab1 = this.props.machine.progressValue[0]
        const tab2 = this.props.machine.progressValue[1]
        const tab3 = this.props.machine.progressValue[2]
        
        return (            
            <ContentWrapper>
                {/* START form */}
                <div className="block-center col-md-8">
                    <Form
                    innerRef={this.formRef}
                    className="form-horizontal" 
                    name="formMachine" 
                    onSubmit={this.handleSubmit}
                    >
                        <Card className="card-default">
                            <CardHeader className="pb-0">
                                <Row>
                                    <div className="col pr-0"> <Progress color="success-light" value={tab1}/> </div>
                                    <div className="col p-0"> <Progress color="success-light" value={tab2}/> </div>
                                    <div className="col pl-0"> <Progress color="success-light" value={tab3}/> </div>
                                </Row>
                            </CardHeader>                            
                            <CardBody className="pt-0 pb-0">         
                                <Nav tabs justified >
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.machine.activeStep === '1'
                                        })}
                                        onClick={this.toggleStep('1')}
                                        >
                                        <h4 className="text-left my-2">Adresse</h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.machine.activeStep === '2'
                                        })}
                                        onClick={this.toggleStep('2')}
                                        >
                                        <h4 className="text-left my-2">Modele</h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.machine.activeStep === '3'
                                        })}
                                        onClick={this.toggleStep('3')}
                                        >
                                        <h4 className="text-left my-2">Visualisation</h4>
                                        </NavLink>
                                    </NavItem>
                                </Nav>
                            </CardBody>    
                            <TabContent activeTab={this.props.machine.activeStep}>
                                {/*nom, zone, adresse */}
                                <TabPane id="tabPane1" tabId="1">
                                    <div className="pt-3 mb-3">
                                        {/*nom */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nom</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="nom"
                                                name="nom" 
                                                placeholder="Nom" 
                                                invalid={this.hasError('formMachine','nom','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={nom}
                                                />
                                                <span className="invalid-feedback">Veuillez entrer le nom de la machine</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*adresse */}
                                        <Card/>
                                        <h4> Adresse </h4>
                                        <Card/>
                                        <div className="flex-direction-column">
                                        {/*pays */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Pays</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="pays"
                                                name="pays" 
                                                placeholder="Pays" 
                                                invalid={this.hasError('formMachine','pays', 'required')}
                                                onChange={this.validateOnChange}
                                                value={pays}
                                                data-validate='["required"]' 
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                                                                            
                                            </div>
                                        </FormGroup>
                                        {/*ville */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Ville</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="ville"
                                                name="ville" 
                                                placeholder="Ville" 
                                                invalid={this.hasError('formMachine','ville', 'required')}
                                                onChange={this.validateOnChange}
                                                value={ville}
                                                data-validate='["required"]'
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>                                                                                           
                                        </FormGroup>
                                        {/*quartier */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Quartier</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text" 
                                                id="quartier"
                                                name="quartier" 
                                                placeholder="Quartier" 
                                                invalid={this.hasError('formMachine','quartier', 'required')}
                                                onChange={this.validateOnChange}
                                                value={quartier}
                                                data-validate='["required"]'
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>
                                        {/*rue */}                                                                                
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Rue</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="rue" 
                                                name="rue" 
                                                placeholder="rue" 
                                                invalid={this.hasError('formMachine','rue', 'required')}
                                                onChange={this.validateOnChange}
                                                value={rue}
                                                data-validate='["required"]'
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>                                                                                                                                                                       
                                        </div>                                       
                                        {/*zone */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Zone</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="zone"
                                                name="zone" 
                                                placeholder="Zone" 
                                                invalid={this.hasError('formMachine','zone')}
                                                onChange={this.validateOnChange}
                                                value={zone}
                                                />
                                            </div>
                                        </FormGroup>
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        {/*<Button color="secondary">Previous</Button>*/}
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        onClick={this.toggleStep('2')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>   
                                </TabPane>
                                {/*modele */}                        
                                <TabPane id="tabPane2" tabId="2">
                                    <div className="pt-3 mb-3">
                                        {/*libelle */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Libelle</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="select"
                                                id="libelle"
                                                name="libelle" 
                                                placeholder="libelle" 
                                                onChange={this.validateOnChange}
                                                invalid={this.hasError('formMachine','libelle','required')}
                                                data-validate='["required"]'
                                                value={libelle}
                                                >
                                                    {this.props.machine.modeles.map((modele) =>
                                                        <option> {modele.libelle} </option>
                                                    )}
                                                </Input>
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*description */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Description</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="description"
                                                name="description" 
                                                placeholder="Description" 
                                                invalid={this.hasError('formMachine','description','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]' 
                                                value={description}
                                                disabled
                                                />
                                            </div>
                                        </FormGroup>
                                        {/*capacite */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label"> Capacite </label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="capacite"
                                                name="capacite" 
                                                placeholder="Capacite"
                                                invalid={this.hasError('formMachine','capacite','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={capacite}
                                                disabled
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>
                                        {/*dimension */}                                
                                        <Card/>
                                        <h4> Dimension </h4>
                                        <Card/>
                                        <hr/>
                                        { /*longueur */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label"> Longueur </label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="longueur"
                                                name="longueur" 
                                                placeholder="Longueur" 
                                                invalid={this.hasError('formMachine','longueur','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'  
                                                value={longueur}
                                                disabled
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>
                                        {/*largeur */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label"> Largeur </label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="largeur"
                                                name="largeur" 
                                                placeholder="Largeur"
                                                invalid={this.hasError('formMachine','largeur','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]' 
                                                value={largeur}
                                                disabled
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>
                                        {/*hauteur */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label"> Hauteur </label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="hauteur"
                                                name="hauteur" 
                                                placeholder="Hauteur"
                                                invalid={this.hasError('formMachine','hauteur','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'   
                                                value={hauteur}
                                                disabled
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </div>
                                        </FormGroup>
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('1')}>
                                            Precedent
                                        </Button>
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        onClick={this.toggleStep('3')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>
                                </TabPane>
                                {/*coordonnees */}
                                <TabPane id="tabPane3" tabId="3">      
                                    <Card/>
                                    <h4> Coordonnees </h4>
                                    <p> Entrer les coordonnes de l'emplacement de votre machine ici</p>
                                    <Card/>                                
                                    <div className="pt-3 mb-3">
                                        {/*latitude */} 
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Latitude</label>
                                            <div className="col-xl-5">
                                                <Input 
                                                type="text"
                                                id="latitude"
                                                name="latitude" 
                                                placeholder="Latitude" 
                                                invalid={this.hasError('formMachine','latitude','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={latitude}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*longitude */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Longitude</label>
                                            <div className="col-xl-5">
                                                <Input 
                                                type="text"
                                                id="longitude"
                                                name="longitude" 
                                                placeholder="longitude" 
                                                invalid={this.hasError('formMachine','longitude','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={longitude}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*carte */}
                                        <MapAdd 
                                         position={coordonnees}
                                        />
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('2')}>
                                            Precedent
                                        </Button>
                                        <Button
                                            type="submit" 
                                            className="ml-auto"
                                            color={nextColor}
                                        >                                              
                                            Terminer
                                        </Button>
                                    </div>
                                </TabPane>
                            </TabContent>                                            
                        </Card>
                        {/* END card */}
                    </Form>
                </div>
            </ContentWrapper>
            );
    }
 
}

export default Machine;
