import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import { Row, Col, Card, CardHeader, CardBody } from 'reactstrap';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';

const containerStyle = {
    width: '100%',
    height: '400px'
};

const DemoMapCustomMarkers = props => (
        <GoogleMap mapContainerStyle={containerStyle} zoom={14} center={props.center}>
                <Marker position={props.center} />
        </GoogleMap>
)

const defaultCenter = {
    lat: 4.64554767,
    lng: 4.64554767
}

class MapAdd extends Component {

    state = {
        customStyle: [
            {featureType:'water',stylers:[{visibility:'on'},
            {color:'#bdd1f9'}]},
            {featureType:'all',elementType:'labels.text.fill',stylers:[{color:'#334165'}]},
            {featureType:'landscape',stylers:[{color:'#e9ebf1'}]},
            {featureType:'road.highway',elementType:'geometry',stylers:[{color:'#c5c6c6'}]},
            {featureType:'road.arterial',elementType:'geometry',stylers:[{color:'#fff'}]},
            {featureType:'road.local',elementType:'geometry',stylers:[{color:'#fff'}]},
            {featureType:'transit',elementType:'geometry',stylers:[{color:'#d8dbe0'}]},
            {featureType:'poi',elementType:'geometry',stylers:[{color:'#cfd5e0'}]},
            {featureType:'administrative',stylers:[{visibility:'on'},{lightness:33}]},
            {featureType:'poi.park',elementType:'labels',stylers:[{visibility:'on'},
            {lightness:20}]},{featureType:'road',stylers:[{color:'#d8dbe0',lightness:20}]}
        ]
    }

    render() {
        const position = this.props.position
        //const lat = parseFloat(latitude);
        //const lng = parseFloat(longitude);
        console.log(position)
        return ( 
            <LoadScript
                googleMapsApiKey="AIzaSyB2gRtnm_h3PmxB6ZhZ1ZBpvwAYiQheb1Y"
            >
                <ContentWrapper>
                    <Card className="card-default">
                        <CardBody>
                            <DemoMapCustomMarkers 
                             center={position}
                             />
                        </CardBody>
                    </Card>
                </ContentWrapper>
            </LoadScript>
        );
    }

}

export default MapAdd;

